    width: 25%;
    padding: 0 14px 33px;
    height: 135px;
  }
}
@media (max-width: 767px) {
  .clients__items--primary .clients__item {
    width: 33.3333%;
    padding: 0 5px 10px;
    height: 112px;
  }
}
.clients__items--secondary {
  margin-top: 7px;
}
.clients__items--secondary .clients__item {
  padding: 0px 15px 26px;
  width: 156px;
  height: 118px;
}
.clients__item {
  flex-shrink: 0;
}
.clients__inner {
  background: #fff;
  height: 100%;
  box-shadow: 0 0 17px rgba(29,29,29,0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px;
}
.clients__inner img {
  display: block;
  max-width: 100%;
  max-height: 100%;
}
.clients__more {
  display: none;
  text-align: center;
  margin-top: 44px;
  width: 100%;
  left: 0;
  opacity: 1;
  transition: all 0.1s ease-in;
}
@media (max-width: 1199px) {
  .clients__more {
    display: block;
  }
}
@media (max-width: 767px) {
  .clients__more {
    margin-top: 58px;
  }
}
.clients__more--hidden {
  position: absolute;
  visibility: hidden;
  opacity: 0;
}
.clients__button {
  min-width: 382px;
}
.test {
  background: url("modules/test/test-bg.jpg") calc(50% - 500px) 100% no-repeat;
  padding: 88px 0 76px;
}
@media (max-width: 1199px) {
  .test {
    padding: 58px 0 28px;
    background: none;
  }
}
@media (max-width: 767px) {
  .test {
    padding: 59px 0 27px;
  }
}
.test .c {
  position: relative;
  z-index: 1;
}
.test__title {
  text-align: center;
  max-width: 960px;
  margin: 0 auto 42px;
}
@media (max-width: 1199px) {
  .test__title {
    margin-bottom: 12px;
  }
}
@media (max-width: 767px) {
  .test__title {
    margin-bottom: 23px;
  }
}
.test__title:before,
.test__title:after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 50%;
  margin-left: -1px;
}
@media (max-width: 1199px) {
  .test__title:before,
  .test__title:after {
    top: auto;
    bottom: 0;
  }
}
.test__title:before {
  width: 7px;
  height: 7px;
  background: #006dfe;
  margin-top: 186px;
}
@media (max-width: 1199px) {
  .test__title:before {
    margin-bottom: -38px;
  }
}
@media (max-width: 767px) {
  .test__title:before {
    margin-bottom: -46px;
  }
}
.test__title:after {
  width: 1px;
  height: 7px;
  background: #1d1d1d;
  margin-top: 179px;
}
@media (max-width: 1199px) {
  .test__title:after {
    margin-bottom: -31px;
  }
}
@media (max-width: 767px) {
  .test__title:after {
    margin-bottom: -39px;
  }
}
.test__sub {
  text-align: center;
  font-size: 24px;
  margin-bottom: 42px;
}
@media (max-width: 1199px) {
  .test__container {
    max-width: none;
    padding: 0;
    position: relative;
    overflow: hidden;
  }
}
.test__buttons {
  display: none;
  align-items: center;
  justify-content: center;
  margin-top: 28px;
}
@media (max-width: 1199px) {
  .test__buttons {
    display: flex;
  }
}
.test__nav {
  margin: 0 22px;
  display: flex;
}
.test__items {
  display: flex;
}
.test__item {
  flex: 1;
  padding-top: 100px;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}
.test__item:after {
  content: '';
  display: block;
  width: 220px;
  height: 20px;
  background: #006dfe;
  margin: auto auto 0;
}
@media (max-width: 1199px) {
  .test__item:after {
    width: 430px;
  }
}
.test__item:hover .test__image {
  transform: translate(-50%, -50%) rotate(-15deg);
}
@media (max-width: 1199px) {
  .test__item {
    flex: none;
    padding-top: 120px;
  }
}
.test__inner {
  position: relative;
  text-align: center;
  max-width: 350px;
  margin: 0 auto;
  flex: 1;
}
.test__inner:before,
.test__inner:after {
  content: '';
  display: block;
  position: absolute;
  width: 20px;
  height: 10px;
  background: #006dfe;
  bottom: 0;
  left: 50%;
}
.test__inner:before {
  margin-left: -110px;
}
@media (max-width: 1199px) {
  .test__inner:before {
    margin-left: -215px;
  }
}
.test__inner:after {
  margin-left: 90px;
}
@media (max-width: 1199px) {
  .test__inner:after {
    margin-left: 195px;
  }
}
@media (max-width: 1199px) {
  .test__inner {
    max-width: 580px;
  }
}
@media (max-width: 767px) {
  .test__inner {
    max-width: 480px;
  }
}
.test__image {
  position: absolute;
  left: 50%;
  top: 10px;
  transform: translate(-50%, calc(-50% + 10px));
  transition: all 0.1s ease-in;
}
.test__image.lazy--visible {
  transform: translate(-50%, -50%);
}
.test__name {
  font-size: 30px;
  border: 20px solid #006dfe;
  border-bottom: 0;
  width: 220px;
  margin: 0 auto 12px;
  padding: 50px 0 12px;
}
@media (max-width: 1199px) {
  .test__name {
    width: 430px;
  }
}
.test__text {
  font-size: 18px;
  line-height: 36px;
  margin: 0 auto 20px;
}
@media (max-width: 1199px) {
  .test__text {
    margin-bottom: 33px;
  }
}
